<template>
  <div>
    <!--<p class="text-red-500">{{ tags }}</p>-->
    <div class="flex w-full items-center">
      <div v-show="tagsInput === true" class="relative w-full">
        <div class="flex h-8 flex-1 items-center space-x-2 rounded border border-gray-200 bg-white px-4 hover:border-gray-300" :class="{ inputError: validateState === 'error' }">
          <a-tag v-for="tag in tags" :key="tag.name" color="#165dff" class="rounded" closable @close="handleClose(tag)">{{ tag.name }}</a-tag>
          <input v-show="allowCreate === true" v-model="inputValue" @keyup.enter="handleInputConfirm" class="flex-1" @blur="handleInputConfirm" />
        </div>
        <p v-show="validateState === 'error'" class="absolute text-red-500" style="bottom: -20px"></p>
      </div>
      <a-button :type="type" :size="size" :plain="plain" :circle="circle" :status="status" @click="edit_countersign"></a-button>
    </div>
    <el-dialog title="" draggable v-model="dialogVisible" :show-close="false">
      <div class="flax mb-5 space-x-4">
        <el-table ref="memberTables" :data="memberList" @selection-change="select" :row-key="getRowKeys">
          <el-table-column type="selection" width="50"></el-table-column>
          <el-table-column label="" prop="id"></el-table-column>
          <el-table-column label="" prop="name"></el-table-column>
          <el-table-column label="" prop="department"></el-table-column>
        </el-table>
      </div>
      <div class="flex w-full items-center justify-center space-x-2">
        <a-button status="primary" @click="memberListDialog_Cancel"></a-button>
        <a-button v-if="rulesOption === true" type="primary" @click="memberListDialog_Confirm"></a-button>
        <a-button v-else type="primary" @click="memberListDialog_Confirm2"></a-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { GenNonDuplicateID } from "@/common/until";
import { userList } from "@/views/config/api";
export default {
  name: "testPage",
  components: {},
  props: {
    status: {
      type: String,
      default: null,
    },
    circle: {
      type: String,
      default: null,
    },
    plain: {
      type: String,
      default: null,
    },
    size: {
      type: String,
      default: null,
    },
    type: {
      type: String,
      default: null,
    },
    // 
    allowCreate: {
      type: Boolean,
      default: true,
    },
    // 
    tagsInput: {
      type: Boolean,
      default: true,
    },
    // 
    rulesOption: {
      type: Boolean,
      default: true,
    },
    //
    tagsValue: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      dialogVisible: false,
      tags: this.tagsValue,
      inputValue: "",
      memberList: [],
      selectTags: [],
      validateState: "success",
    };
  },
  methods: {
    // API
    getApi() {
      userList().then((res) => {
        this.memberList = res.data;
        //object
        /*for (let stage of this.memberList) {
          Object.assign(stage, { isSelected: false });
        }*/
      });
    },
    // 
    select(val) {
      this.selectTags = JSON.parse(JSON.stringify(val));
      console.log(this.selectTags);
    },
    //
    edit_countersign() {
      this.dialogVisible = true;
      // this.tags = JSON.parse(JSON.stringify(row));
      // this.editForm = row;

      // el-tablecheckbox
      this.$nextTick(() => {
        this.$refs.memberTables.clearSelection();
        this.tags.forEach((item) => {
          this.memberList.forEach((subItem) => {
            if (item.id === subItem.id) {
              this.$refs.memberTables.toggleRowSelection(subItem);
            }
          });
        });
      });
    },
    // ()
    memberListDialog_Confirm() {
      this.tags = this.selectTags;
      this.dialogVisible = false;

      /**  */
      if (this.tags.length === 0) {
        this.validateState = "error";
      } else {
        this.validateState = "success";
        const rows = this.tags;
        this.$emit("sand-form", rows);
      }
    },
    // 
    memberListDialog_Confirm2() {
      this.tags = this.selectTags;
      this.dialogVisible = false;
      const rows = this.tags;
      this.$emit("sand-form", rows);
    },
    //
    memberListDialog_Cancel() {
      this.dialogVisible = false;
    },

    // tag
    handleClose(tag) {
      this.tags.splice(this.tags.indexOf(tag), 1);
      this.selectTags.splice(this.tags.indexOf(tag), 1);
    },
    // input
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.tags.push({
          name: inputValue,
          id: GenNonDuplicateID(8),
          department: "",
        });
      }
      this.inputValue = "";
    },

    getRowKeys(row) {
      return row.id;
    },

    test() {
      this.$nextTick(() => {
        this.tags.forEach((item) => {
          this.memberList.forEach((subItem) => {
            if (item.id === subItem.id) {
              this.$refs.memberTables.toggleRowSelection(subItem, true);
            }
          });
        });
      });
    },
  },
  mounted() {
    this.getApi();
    this.test();
  },
  created() {},
};
</script>

<style scoped lang="scss">
.inputError {
  border: #f56c6c 1px solid;
}
</style>
